import React from "react";
import cl from'./Menu.module.css';
import {NavLink} from'react-router-dom';


const Menu = () =>{
return <div className={cl.nav}>
	<NavLink to="/katalog" className={cl.item} className = {navData => navData.isActive ? cl.active : cl.item }></NavLink>
	<NavLink to="/building" className={cl.item} className = {navData => navData.isActive ? cl.active : cl.item }></NavLink>
	<NavLink to="/proeckting" className={cl.item} className = {navData => navData.isActive ? cl.active : cl.item }></NavLink>
	<NavLink to="/servis" className={cl.item} className = {navData => navData.isActive ? cl.active : cl.item }></NavLink>
	<NavLink to="/portfolio" className={cl.item} className = {navData => navData.isActive ? cl.active : cl.item }></NavLink>
	<NavLink to="/ontacts" className={cl.item} className = {navData => navData.isActive ? cl.active : cl.item }></NavLink>
	<NavLink to="/about" className={cl.item} className = {navData => navData.isActive ? cl.active : cl.item }> </NavLink>
	
</div>
}

export default Menu;