import React from "react";
import {Link} from'react-router-dom';
import cl from'./Auth.module.css';


const DontLogin = () =>{

return  (
	<div className={cl.main}>    
	<div className={cl.minTitle}>    </div>
	<Link className={cl.button} to='/login'></Link>
	</div>

)
}


export default DontLogin;